<template>
  <h1>正在加载中...</h1>
</template>

<script>
export default {
  created() {
    this.unWatch = this.$watch(
      () => this.$store.getters["loginUser/status"],
      (status) => {
        if (status !== "loading") {
          const resultUrl = this.$route.query.resultUrl || "/index.html";
          this.$router.push(resultUrl).catch(() => {});
        }
      },
      { immediate: true }
    );
  },
  destroyed() {
    this.unWatch();
  },
};
</script>

<style scoped>
h1 {
  text-align: center;
}
</style>
